.navbar {
    width: 100%;
    background-color: #313030;
    overflow: hidden;


    &__list {
        // width: 545px;
        display: flex;
        align-items: center;
        list-style: none;
        justify-content: center;
    }

    &__link {
        text-decoration: none;
        text-transform: uppercase;
        color: #e0e0e0;
        font-size: 16px;
        font-weight: 600;

        
    }

    &__item {
        padding: 45px 35px 40px 35px;
        transition-property: background-color;
        transition-duration: 500ms;
        

        &:hover {
            background-color: $second;
        }
    }
}


.advantages__svg {
    height: 40px;
    fill: $second;
}

@media (max-width: 680px) {
    .navbar__item {
        padding: 45px 22px 40px 22px;
    }
}

@media (max-width: 515px) {
    .navbar {

        &__list {
            flex-wrap: wrap;
            overflow: hidden;
        }

        &__item {
            flex: 1 2 25%;
            text-align: center;
        }
        
        &__item:nth-child(odd) {
            padding-right: 34px; 
        }

        &__item:nth-child(2n) {
            padding-right: 37px; 
        }
    }
}