$subtitleColor: #737373;

.heading__wrap {
    max-width: 730px;
    padding-bottom: 135px;
}

.contact {
    width: 100%;
    position: relative;
    padding-bottom: 110px;

    &__heading {
        font-size: 38.34px;
        font-family: "Open Sans";
        color: $titleGray;
        font-weight: 700;
        text-transform: uppercase;
        line-height: 1.2;
        text-align: left;
        margin-top: 105px;
        margin-bottom: 45px;
    }

    &__subtitle {
        font-size: 14.606px;
        font-family: "Open Sans";
        color: $subtitleColor;
        line-height: 1.75;
        text-align: left;   
        position: relative;

        &::after {
            display: inline-block;
            content: '';
            background-color: $second;
            position: absolute;
            width: 132px;
            height: 3px;
            left: 0;
            top: 130px;
        }
    }

    &__info-block {
        display: flex;
        justify-content: space-between;
    }

    &__info {
        padding: 140px 40px 140px 75px;
        background-color: #2d2d2d;
        position: absolute;
        right: 0;
        bottom: auto;
        height: 491px;
        width: 575px;

        &-title {
            font-size: 12.682px;
            font-family: "Open Sans";
            color: rgba(255, 255, 255, 0.8);
            font-weight: 700;
            text-transform: uppercase;
            line-height: 1.892;
            text-align: left;  
            margin-bottom: 25px;
        }

        &-list {
            display: flex;
            flex-direction: column;
            list-style: none;

            &__item {
                font-size: 16px;
                font-family: "Open Sans";
                line-height: 1.75;
                text-align: left;
                margin-bottom: 25px;

                a {
                    text-decoration: none;
                    color: #e0e0e0;
                }
            }
        }
    }

    &__background {
        position: absolute;
        overflow: visible;
        width: 575px;
        height: 483px;
        top: 0;
        left: 0;
        z-index: 1;
    }
}

.form {
    margin-right: 25px;
} 

@media (max-width: 1420px) {
    .heading__wrap {
        margin: 0 auto;
    }

    .contact {

        &__heading {
            text-align: center;
        }

        &__subtitle {
            text-align: center;

            &::after {
                left: 50%;
                transform: translateX(-50%);
            }
        }

    

        &__info {
            width: 505px;
            top: 260px;
        }
    }

    .form {
        width: 680px;
    }
}

@media (max-width: 1180px) {
    .heading__wrap {
        margin: 0 auto;
    }

    .contact {
        padding-bottom: 260px;

        &__subtitle {
    
            &::after {
                top: 170px;
            }
        }
    

        &__info {
            width: 100%;
            height: 148px;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            top: 920px;
            position: absolute;
            margin: 0 auto;
            margin-top: 25px;
            padding: 75px 53px 75px 75px;

            &-title { 
                margin-bottom: 12px;
            }

            &-list__item {
                margin-bottom: 0px;
            }

            &-block {
                flex-direction: column;
            }
        }
    }

    .form {
        width: 680px;
        margin: 0 auto;
    }
}

@media (max-width: 680px) {

    .contact {
        padding-bottom: 360px;

        &__info {
            padding: 110px 53px 110px 53px;
            top: 980px;
        }

        .form {
            width: 95%;
            margin: 0 auto;
        }
    }

}