$linkColor: #333333;

.team {

    &__main-header {
        @include visually-hidden; 
    }

    &__header {
        max-width: 435px;
        text-align: left;
        margin-bottom: 100px;
    }

    &__heading {
        font-size: 38.34px;
        font-family: "Open Sans";
        color: $titleGray;
        font-weight: 700;
        text-transform: uppercase;
        line-height: 1.2;
        margin-bottom: 40px;
    }

    &__subtitle {
        font-size: 14.606px;
        font-family: "Open Sans";
        color: rgba(0, 0, 0, 0.702);
        line-height: 1.75;
        text-align: left;
        position: relative;

        &::after {
            content: '';
            display: block;
            background-color: $second;
            position: absolute;
            width: 132px;
            height: 3px;
            left: 0;
            margin-top: 32px;
        }
    }

    &__info {
        display: flex;
        justify-content: space-between;
        margin-bottom: 110px;
    }

    &__member-image-lg {
        max-width: 320px;
        height: 460px;

        img {
            width: 100%;
            height: 100%;
        }
    }

    &__info-block {
        display: flex;
        flex-direction: column;
        max-width: 870px;
    }

    &__member-name {
        font-size: 27.527px;
        font-family: "Open Sans";
        color: $titleGray;
        text-transform: uppercase;
        line-height: 1.2;
        text-align: left;
        margin-bottom: 35px;
    }

    &__member-info {
        max-width: 640px;
        font-size: 14.606px;
        font-family: "Open Sans";
        color: rgba(0, 0, 0, 0.702);
        line-height: 1.75;
        text-align: left;
        margin-bottom: 42px;
    }

    &__link-list {
        display: flex;
        margin-bottom: 65px;
    }

    &__link-item {
        list-style: none;
        margin-right: 30px;
    }

    &__link {
        text-decoration: none;
        color: $linkColor;
        font-style: italic;

        &:hover {
            color: $second;
            transition-duration: 200ms;
        }
    }

    &__members-gallery {
        display: flex;
    }

    &__members-gallery_item{
      width: 195px;
      height: 215px;
      position: relative;
      overflow: hidden;

      &:hover .team__members-name {
        transform: translateY(0);
        transition-duration: 500ms;
      }
    }

    &__members-gallery_item:not(:last-child) {
        margin-right: 22px;
    }

    &__members-image {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__members-name {
        position: absolute;
        width: 100%;
        left: 0;
        bottom: 0;
        background-color: $black;
        opacity: 0.5;
        height: 37px; 
        display: flex;
        justify-content: center;
        align-items: center;
        text-transform: uppercase;
        transform: translateY(37px);
        cursor: pointer;
    }

}

@media (max-width: 1240px) {
    .team__member-name {
        margin-bottom: 30px;
    }

    .team__member-info {
        margin-bottom: 22px;
    }

    .team__link-list {
        margin-bottom: 27px;
        flex-wrap: wrap;
        justify-content: center;
    }

    .team__members-gallery_item:not(:last-child) {
        margin-right: 0;
    }

    .team__member-image-lg {
        margin-right: 25px;
    }

    .team__members-gallery {
        flex-wrap: wrap;
        justify-content: center;
    }


}

@media (max-width: 1180px) {
    .team__header {
        max-width: 100%;
    }

    .team__heading {
       text-align: center;
    }

    .team__subtitle {
        text-align: center;
    }

    .team__subtitle::after {
        left: 50%;
        transform: translateX(-50%);
        margin-top: 32px;
    }

    .team__info {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .team__member-image-lg {
        margin-right: 0;
        margin-bottom: 20px;
    }

    .team__info-block {
        justify-content: center;
        align-items: center;
    }

    .team__member-info {
        text-align: center;
    }

}

@media (max-width: 360px) {
    .team__member-image-lg {
        height: 415px;
    }

    .team__members-gallery_item {
        width: 145px;
        height: 145px;
    }

}