.tag-panel {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 905px;
    list-style: none;
    margin: 0 auto;


    &__item-link {
        color: #737373;
        font-size: 22px;
        font-weight: 600;
        font-family: "Open Sans";
        text-transform: uppercase;
        line-height: 1.091;
        cursor: pointer;
        transition-property: color;
        transition-duration: 500ms;


        &:hover {
            color: $second;
        }
    }
}

.tag-panel .active {
    color: $second;
}

@media (max-width:880px) {
    .tag-panel {
        flex-direction: column;

        &__item {
            margin-bottom: 25px;
        }
    }
}

