.header {
    background-color: $thirty;
    padding-top: 30px;

    &__top {
        display: flex;
        padding-top: 100px;
        padding-bottom: 110px;
        justify-content: space-between;
    }

    &__title {
        font-size: 60px;
        font-family: "Open Sans";
        font-weight: 800;
        color: #000000;
        text-transform: uppercase;
        line-height: 1.2;
        text-align: left; 
        margin-bottom: 30px;      
    }

    &__subtitle {
        font-size: 14.606px;
        font-family: "Open Sans";
        color: #6f6d6d;
        line-height: 1.75;
        text-align: left;
        display: block;
        width: 520px; 
        padding-bottom: 60px;
    }

    &__video {
        display: block;
        min-width: 550px;
        height: 355px;
        background-color: #c4c0b1;
        box-shadow: -14px -11px 59px 5px rgba(0, 0, 0, 0.29);
    }

}

.btn {
    border: 2px solid $second;
    display: inline-block;
    border-radius: 5px;
    text-align: center;     
    
    display: inline-flex;
    justify-content: center;
    align-items: center;

    transition-property: background-color;
    transition-duration: 500ms;

    &:hover, &:active {
        background-color: darken($second, 15%);
        border-color: darken($second, 15%);
      }
}

.header__btn {
    background-color: $second;
    width: 227px;
    height: 55px; 
}

.header__btn a {
    text-decoration: none;
    color: $white;
}


@media (max-width: 1210px) {
    .header__top {
        flex-direction: column;
        justify-content: center;
        align-items: center;

        padding-top: 50px;
        padding-bottom: 50px;
    }

    .header__info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-left: 15px;
        padding-right: 15px;
    
    }

    .header__title {
        font-size: 48px;
        text-align: center;
    }

    .header__subtitle {
        width: auto;
        text-align: center;
    }

    .header__video {
        margin-top: 45px;
    }
}

@media (max-width: 460px) {
    .header__title {
        font-size: 42px;
    }
}

@media (max-width: 380px) {
    .header__title {
        font-size: 38px;
    }

    .header__video {
        min-width: 0;
        width: 320px;
    }
}



