$productsTitleColor: #585858;

.products {

   &__title {
    border-top: 2px solid #ececec;
    padding-top: 100px;
    font-size: 38.34px;
    font-family: "Open Sans";
    color: $productsTitleColor;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.2;
    text-align: center;
    
   }

   &__subtitle {
    font-size: 14.606px;
    font-family: "Open Sans";
    color: $titleColor;
    line-height: 1.75;
    text-align: center;
    max-width: 520px;
    margin: 0 auto;
    margin-top: 30px;
    position: relative;
    margin-bottom: 103px;

        &:after {
            content: '';
            display: block;
            background-color: $second;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            top: 85px;
            width: 132px;
            height: 3px;
        }
   }


   &__list {
        list-style: none;
        // display: grid;
        // grid-template-columns: repeat(4, 1fr);
        // grid-template-rows: repeat(2, 275px);
        // grid-gap: 21px;
        display: flex; 
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 60px;
        padding-bottom: 115px;

    }

   &__list-item {
       background: #f5f0dd;
       position: relative;
       overflow: hidden;
       background-image: url(https://images.pexels.com/photos/1198817/pexels-photo-1198817.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
       background-size: cover;
       width: 290px;
       height: 290px;
       margin-bottom: 20px;
       
       

       &:hover .products__overlay {
            transition-property: opacity;
            transition-duration: 500ms;
            opacity: 0.5;
       }
   }

    &__list-item:nth-child(odd) {
        margin-right: 10px;
    }

   &__overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #dacece;
    opacity: 0;
   }
}

@media (max-width:1180px) {
    .products {

        &__title {
            font-size: 32.34px;
        }

        &__list {
            padding-bottom: 15px;
        }
    }
}

@media (max-width:925px) {
    .products {

        &__list {
            justify-content: center;
        }
    }
}

@media (max-width:640px) {
    .products {

        &__list {
            justify-content: center;
        }

        &__list-item:nth-child(odd) {
            margin-right: 0;
        }
    }
}