$titleColor: #737373;

.features {
    padding-top: 110px;
    padding-bottom: 108px;
    background-color: $white;
    display: flex;

    &__offer {
        display: flex;
        flex-direction: column;
        max-width: 535px;
        padding-bottom: 108px;
        margin-right: 76px;
    }

    &__offer-title {
        font-size: 42.216px;
        font-family: "Open Sans";
        color: $titleGray;
        font-weight: 700;
        text-transform: uppercase;
        line-height: 1.31;
        text-align: left;
    }

    &__offer-subtitle {
        font-size: 14.606px;
        font-family: "Open Sans";
        color: #000000b3;
        line-height: 1.75;
        text-align: left;
        margin-top: 48px;
    }

    &__btn {
        margin-top: 55px;
        width: 227px;
        height: 55px; 
    }
    
    &__btn a {
        font-size: 16.78px;
        font-family: "Open Sans";
        color: $second;
        font-weight: 700;
        line-height: 1.878;
        text-align: center; 
        text-decoration: none;     

    }

    &__btn:hover a {
        transition-property: color;
        transition-duration: 500ms;
        color: $white;
    }

    &__list {
        display: grid;
        // grid-template-columns: 1fr 1fr;
        grid-template-columns: repeat(2, 295px);
        grid-gap: 30px;
    }


    &__list-item {
        text-align: left;
        border: 2px solid transparent;
        border-radius: 4px;
        padding: 15px;
        // max-width: 290px;
        transition-property: border;
        transition-duration: 500ms;

        &:hover h3, p, svg {
            color: darken($titleColor, 15%);
        }

        &:hover {
            border-color: $second;
        }
    }

    &__svg {
        height: 40px;
        width: 40px;
        fill: $second;
        transition-property: color;
        transition-duration: 500ms;
    }

    &__item-title {
        margin-top: 25px;
        font-size: 27px;
        font-family: "Open Sans";
        color: $titleColor;
        line-height: 1.2;
        text-align: left;
        transition-property: color;
        transition-duration: 500ms;
    }

    &__item-subtitle {
        margin-top: 20px;
        font-size: 16px;
        font-family: "Open Sans";
        color: $titleColor;
        line-height: 1.75;
        text-align: left;
        transition-property: color;
        transition-duration: 500ms;
    }

}

@media (max-width: 1200px) {
    .features {
        padding-left: 50px;
        padding-right: 50px;
    }

    .features__list {
        grid-template-columns: repeat(2,280px);
        grid-gap: 25px;
    }

}

@media (max-width: 1100px) {
    .features {
        padding-left: 50px;
        padding-right: 50px;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        &__offer {
            margin-right: 0;
        }
    }

    .features__list {
        grid-template-columns: repeat(2,280px);
        grid-gap: 25px;
    }

}

@media (max-width: 780px) {
    .features {
        padding-top: 70px;
        padding-bottom: 70px;

        &__offer {
            padding-bottom: 70px;

        }
    }

    .features__list {
        grid-template-columns: repeat(2,280px);
        grid-gap: 10px;
    }

}

@media (max-width: 620px) {
    .features {
        padding-top: 70px;
        padding-bottom: 70px;

        &__offer {
            padding-bottom: 70px;

        }
    }

    .features__list {
        grid-template-columns: repeat(1, auto);
        grid-gap: 10px;
    }

}

@media (max-width: 380px) {
    .features {
        padding-left: 20px;
        padding-right: 20px;

        &__offer {
            justify-content: center;
            align-items: center;
        }

        &__offer-title {
            font-size: 36.216px;
        }

        }
    }

