$formColor: #e5e5e5;

label {
    @include visually-hidden; 
}

form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

input,textarea {
    border: none;
    outline: none;
    background-color: $formColor;
    padding-top: 27px;
    padding-left: 50px; 
    border-radius: 10px;
    resize: none;
}

input {
    padding-bottom: 27px;
}

::placeholder {
    color: #676767;
}

.form {
    width: 730px;
}

#form-name {
    width: 335px;
}

#form-email {
    width: 335px;
}

.message {
    width: 100%;
    padding-top: 47px;
    padding-bottom: 47px;
}

#form-message {
    width: 100%;
    height: 250px;
}

.form-btn {
    background-color: $second;
    width: 100%;
    height: 72px;
}

.form-btn a {
    text-decoration: none;
    font-size: 18px;
    font-family: "Open Sans";
    color: $white;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.892;
}

@media (max-width: 1420px) {

    .form {
        width: 620px;

        &__input {
            width: 100%;
        }

        &__input:first-child {
            margin-bottom: 25px;
        }
    }

    #form-name {
        width: 100%;
    }
    
    #form-email {
        width: 100%;
    }
}