@charset "UTF-8";

/**
 *  
 **/

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	
}

html {
	font-size: 16px;
	color: $white;
}

body {
	font-family: 'Open Sans', sans-serif;
	overflow-wrap: break-word;
	word-wrap: break-word;
}

.container {
	@include min-max(320px, 1280px, 10px);
}

.wrap {
	@include min-max(320px, 1200px, 0px);
	margin: 0 auto;
}