.subscribe {
    background: url(https://images.pexels.com/photos/1806123/pexels-photo-1806123.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
    background-attachment: fixed;
    background-position: center center;
    background-repeat: no-repeat;

    &__title {
        padding-top: 140px;
        font-size: 42px;
        font-family: "Open Sans";
        color: rgba(255, 255, 255, 0.9);
        font-weight: 700;
        line-height: 1;
        text-align: center;
    }

    &__subtitle {
        padding-top: 40px;
        font-size: 14.606px;
        font-family: "Open Sans";
        color: $white;
        line-height: 1.75;
        text-align: center;
    }

    form {
        padding-top: 65px;
        padding-bottom: 140px;
        justify-content: center;
    }

    input:first-child {
        padding: 30px 0 30px 35px;
        margin-right: 30px;
        width: 520px;
        background-color: $white;
    }
    
    &__btn-link {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 60px;
        background-color: $second;
        text-transform: uppercase;
        color: $white;
        font-weight: 700;
        
        &:hover {
            background-color: darken($second, 15%);
            transition-property: background-color;
            transition-duration: 500ms;
        }
    }


}


@media (max-width: 790px) {
    .subscribe {

        input:first-child {
            margin-right: 0px;
        }
        
        &__btn-link {
            padding: 30px 60px;
            background-color: $second;
            margin-top: 25px;
        }
    }

}