$footerColor: #313030;

.footer {
    background-color: $footerColor;

    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 50px 35px;
    }

    &__copyright {
        font-size: 16px;
        font-family: "Open Sans";
        color: #b8b8b8;
        line-height: 1.625;  
    }

    &__social {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 186px;

    }
    
    &__social-link {
        width: 33px;
        height: 33px;
        background-color: #ebebeb;
        border-radius: 50%;
        list-style: none;
        position: relative;
        transition-property: color;
        transition-property: background-color;
        transition-duration: 500ms;

        &:hover {
            background-color: $footerColor;
        }

        &:hover .footer__svg {
            fill: #ebebeb;
        }
    }

    &__svg {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        /* transform: translateY(-50%); */
        height: 21px;
        width: 23px;
        transition-property: fill;
        transition-duration: 500ms;   
        fill: $footerColor;
    }
}

@media (max-width: 640px) {
    .footer__wrap {
        flex-direction: column;
    }

    .footer__copyright {
        margin-bottom: 25px;
        text-align: center;
    }
}